'use strict';

import Base from './base.js';

export default class extends Base {
  /**
   * index action
   * @return {Promise} []
   */
  indexAction(){
    return this.display();
  }

  async autologinAction() {
    const userInfo = await this.model('appusers').select();

    let random = parseInt(Math.random() * 10);

    let user = userInfo[random];

    await this.session('userInfo', user);

    this.redirect('/desktop');

    return this.success(user);
  }

  async logoutAction() {
    await this.session('userInfo', null);

    //this.redirect('/desktop');
    return this.success('logout success');
  }
}